export default {
  props: {
    where: {
      type: String,
      default: ''
    }
  },
  methods: {
    back () {
      switch (this.where) {
        case '':
          this.$router.back()
          break
        case 'peopleCenter':
          this.$router.push(`/${this.where}`)
          break
        default:
          this.$router.back()
      }
    }
  }
}



